<template>
	<div class="personal-content-body">
		<div class="content">
			<div class="content-title clear">
				<div class="left">审批列表</div>
			</div>
			<div class="historyapply-group">
				<div class="clear table-group-title">
					<el-input placeholder="搜索" v-model="keyWord" class="header-right-time left" :clearable='true' @change="getListData(1)">
						<i slot="prefix" class="el-input__icon el-icon-search "></i>
					</el-input>
					<div class="historyapply-group-top-dropdown select-normal left">
						<el-select v-model="status_str" filterable placeholder="审批类型" @change="changeApplyType">
							<el-option v-for="item in applytypelist" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
					<div class="right" @click="toCreateApply">
						<div class="table-group-title-btn btn-blue">
							<img src="../../images/createapply_icon_create.png">新建审批
						</div>
					</div>
					<div class="clear"></div>
					<!--<div class="breadcrumb">
						<el-breadcrumb separator-class="el-icon-arrow-right">
							<el-breadcrumb-item v-for="(item,index) in routeList" :key="index" :to="{name:item.name,query:item.query}">{{item.name_str}}</el-breadcrumb-item>
						</el-breadcrumb>
					</div>-->
					<div class="line"></div>
				</div>
				<div class="historyapply-group-tablebox">
					<div class="table-normal-box table">
						<el-table :data="list" border style="width: 100%">
							<el-table-column prop="" label="序号" width="80">
								<template slot-scope="scope">
									{{ scope.$index + 1 }}
								</template>
							</el-table-column>
							<el-table-column sortable prop="number" label="审批编号" width="200">
							</el-table-column>
							<el-table-column sortable prop="title" label="审批标题" width="200">
							</el-table-column>
							<el-table-column sortable prop="temName" label="审批类型" width="120">
							</el-table-column>
							<el-table-column sortable prop="name" label="审批申请人" width="120">
							</el-table-column>
							<el-table-column sortable prop="add_time" label="审批申请时间" width="180">
							</el-table-column>
							<el-table-column sortable prop="subscribeFund" label="审批状态" width="240">
								<template slot-scope="scope">
									{{ statusList[scope.row.status] }}
								</template>
							</el-table-column>
							<el-table-column fixed="right" label="操作" width="200">
								<template slot-scope="scope">
									<el-button @click="gotoViewapply(scope.row.approval_id,scope.row.status,scope.row.mark,scope.row.template_id,scope.row.type)" type="text" size="small">查看</el-button>
								</template>
							</el-table-column>
							<template slot="empty">
								<div class="table-nodata flex_start">
									<img src="../../images/table_nodata.png">
									<span>没有对应数据</span> 
								</div>
							</template>
						</el-table>
						<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
    						</el-pagination>
						
						
						
						
						
						
						
						
						<!--<table class="table-normal">
							<tr>
								<th width="40">序号</th>
								<th width="80">审批编号</th>
								<th width="140">审批标题</th>
								<th width="80">审批类型</th>
								<th width="100">审批申请人</th>
								<th width="100">审批申请时间</th>
								<th width="90">审批状态</th>
								<th width="120">操作</th>
							</tr>
							<tr v-for="(item,index) in list" :key="item.seq">
								<td>{{pageSize*(currentPage-1)+index+1}}</td>
								<td>{{item.number}}</td>
								<td>{{item.title}}</td>
								<td>{{item.temName}}</td>
								<td>{{item.name}}</td>
								<td>{{item.add_time}}</td>
								<td>{{statusList[item.status]}}</td>
								<td>
									<span class="table-link" @click="gotoViewapply(item.approval_id,item.status,item.mark,item.template_id,item.type)">查看</span>
								</td>
							</tr>
						</table>
						<div class="table-nodata" v-if="total==0">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>-->
					</div>
					<!--<div class="pagination-normal" v-if="total!=0">
						<el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
						</el-pagination>
					</div>-->
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import {
		getApplyList,
		projectLsit,
		showCopy
	} from '../../config/api'
	import {
		dialogConfirmHasbtn,
		dialogConfirm
	} from '../../config/mUtils'
	export default {
		data() {
			return {
				list: [],
				routeList: [],
				pageType: 1, //1=>一级2=>项目管理页面进入3=>行政管理页面进入
				projectId: '', //公司id
				projInfo: '',
				pageSize: 10,
				keyWord: '',
				total: 0,
				aType: 1, //审批分组
				template_id: '', //模版id
				status: 0, //审批类型
				status_str: '全部', //审批类型
				applytype1: 1, //申请类型（废弃）
				applytypeval: '待我审批',
				applytypelist: [{
						id: 0,
						name: '全部'
					},
					{
						id: 1,
						name: '待我审批'
					}, {
						id: 2,
						name: '我已审批'
					}, {
						id: 3,
						name: '我发起的审批'
					}, {
						id: 4,
						name: '抄送我的'
					}
				],
				applyType: {
					1: '待我审批',
					2: '我已审批',
					3: '我发起的审批',
					4: '抄送我的',
				},
				statusList: {
					'-1': '审批不通过',
					0: '待审批',
					1: '审批中',
					2: '审批人都通过',
					3: '待处理',
					4: '审批通过',
					5: '撤销成功'
				},
				currentPage: 1,
			}
		},
		watch: {
			// $route(to, from) {
			//   this.initPage();
			// }
		},
		created() {
			if(this.routeList.length != 0) {} else {
				this.routeList = this.$route.meta.routeList;
				this.routeList.forEach(element => {
					if(element.name_str == '行政管理') {
						element.name_str = this.$route.query.aName;
					}
				});
			}
			this.initData();
		},
		mounted() {},
		methods: {
			initPage() {
				this.routeList = this.$route.meta.routeList;
				this.routeList.forEach(element => {
					if(element.name_str == '行政管理') {
						element.name_str = this.$route.query.aName;
					}
				});
				this.initData();
			},
			initData() {
				//获取页面入口（1=>一级2=>项目管理页面进入3=>行政管理页面进入）
				if(!this.$route.query.from) {} else {
					this.pageType = this.$route.query.from
				}
				//从项目进入
				if(this.pageType == 2) {
					if(!this.$route.query.projectId) {
						this.$router.push({
							name: 'investProjectManageList'
						})
					} else {
						this.projectId = this.$route.query.projectId;
						//获取项目信息
						projectLsit({
							page: 1,
							number: 10,
							id: this.projectId
						}).then(res => {
							if(res.data.msgNo == 1) {
								this.projInfo = res.data.payload.data[0]
							}
						})
					}
				} else if(this.pageType == 3) {
					if(!this.$route.query.template_id) {
						this.template_id = ''
					} else {
						this.template_id = this.$route.query.template_id;
					}
				}
				//获取模版类别
				if(!this.$route.query.aType) {
					this.aType = '';
				} else {
					this.aType = this.$route.query.aType
				}
				//状态
				if(!this.$route.query.status) {
					this.status = 0;
					this.status_str = "全部";
					this.getListData(1);
				} else {
					this.status = this.$route.query.status;
					this.status_str = this.applyType[this.status];
				}
				//if
				this.routeList.forEach(element => {
					if(element.name_str == '审批列表') {
						if(element.query.current == undefined || parseInt(element.query.current) == 'NAN') {} else {
							this.getListData(parseInt(element.query.current));
						}
					}
				});
			},
			currentChange(val) {
				if(this.total > this.pageSize) {
					this.getListData(val)
				}
			},
			//获取数据
			getListData(val) {
				this.list = [];
//				this.currentPage = val;

				let params = {
					kind: this.status, //下拉选择的状态
					status: this.status, //下拉选择的状态
					page: this.currentPage,
					pageSize: this.pageSize,
					query: this.keyWord,
					type: this.aType,
					tid: this.template_id,
					company_id: this.projectId
				}

				getApplyList(params).then(res => {
					this.changeRouterData();
					let data = res.data.payload;
					this.total = data.total;
					let list = data.data;
					if(this.status == 0) {
						list = list.slice(this.currentPage * this.pageSize - this.pageSize, this.currentPage * this.pageSize);
					}
					list.forEach(item => {
						item.add_time = new Date(item.add_time * 1000).Format('yyyy-MM-dd')
					})
					if(list.length != 0) {
						this.list = list;
					}

				})
			},
			changeRouterData() {
				this.routeList.forEach(element => {
					if(element.name_str == '审批列表') {
						let newObj = element.query;
						newObj.status = this.status;
						newObj.current = this.currentPage;
						this.$router.push({
							name: element.name,
							query: newObj,
						})
					}

				});
			},
			changeApplyType(val) {
				this.status = val;
				this.currentPage = 1;
				this.getListData(1);
			},
			//前往查看页面
			/***************
			 * template_id:模版id
			 * type：属于那一个类型的模版如：或 出勤休假 或 用印审批 或 签字审批
			 * 
			 */
			gotoViewapply(id, status, mark, template_id, type) {
				if(this.status == 1 || this.status == 2 || this.status == 4) {
					status = 2 //其他的
				} else if(this.status == 0) {
					status = 3 //全部的 
				} else {
					status = 1 //我发起的
				}
				let name = 'viewApplyNew';
				if(this.pageType == 2) {
					this.$router.push({
						name: name,
						query: {
							approval_id: id,
							status: status,
							from: this.pageType,
							template_id: template_id,
							aType: type,
							projectId: this.projectId
						}
					})
				} else {
					this.$router.push({
						name: name,
						query: {
							approval_id: id,
							status: status,
							from: this.pageType,
							template_id: template_id,
							aType: type,
							aName: this.$route.query.aName,
						}
					})
				}
			},
			//新建审批
			toCreateApply() {
				this.$router.push({
					name: 'administrativeManageHomepageNew',
				})
			},
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getListData()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getListData()
			},
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/modal.scss';
	.content {
		width: 100%;
		min-height: 100%;
		min-width: 1000px;
		background-color: white;
		padding: 30px 26px;
	}
	
	.table-group-title {
		padding: 0;
	}
	
	.content-title {
		width: 100%;
		.left{
		    font-weight: 400;
		 }
	}
	
	.histroyapply-icon {
		width: 15px;
		height: 15px;
		background: url('../../images/histroyapply_icon.png') no-repeat;
		display: inline-block;
		float: left;
		margin: 2px 6px 0px 0px;
	}
	
	.historyapply-group {
		width: 100%;
		/*border: 1px solid #E4E4E4;*/
		padding: 18px 0 16px;
	}
	
	.historyapply-group-total {
		font-size: 18px;
		color: #3C98E8;
		font-weight: bold;
		line-height: 1em;
		margin-bottom: 35px;
	}
	
	.historyapply-group-tablebox {
		// padding: 0px 18px 0px 14px;
		/*padding: 0px 50px;*/
		min-height: 440px;
	}
	
	.table-normal-box {
		min-height: 440px;
	}
	
	.pagination-normal {
		text-align: center;
		margin-top: 20px;
	}
	
	.header-right-time {
		margin-right: 10px;
		position: relative;
	}
	
	.header-right-time:nth-child(2) {
		margin-right: 0px;
	}
	
	.creditCz-box span:hover {
		cursor: pointer;
		color: #3C98E8;
	}
	
	.creditCz-box span:nth-child(2) {
		//padding: 0 10px;
	}
	
	.btn-cancle {
		border: 1px solid #3C98E8;
		color: #3C98E8;
		margin-right: 20px;
	}
	
	.form-group-inputbox {
		width: 470px;
		margin-right: 0px;
	}
	
	.breadcrumb {
		margin-top: 19px;
		padding: 0px 14px;
	}
	
	.table-group-title-btn {
		display: flex;
		align-items: center;
		img {
			margin-right: 8px;
		}
	}
	
	.historyapply-group-top-dropdown {
		margin-right: 10px;
	}
</style>


